import React, { useState } from 'react';
import { useStore } from '../hooks/useStore';
import score from '../images/UI_Score_Graphic.png'
import {useSpring, animated} from 'react-spring'


export const ScorePrompt = (props) => {
  const [scorePActive,] = useStore((state) => [
    state.scorePActive,
  ]);

  const { spring } = useSpring({
    spring: scorePActive,
    config: { mass: 5, tension: 800, friction: 50, precision: 0.0001 }
  })

  const scale = spring.to([0, 1], ['0%', '120%'])
  const opacity = spring.to([0, 1], [0, 1])

  return(
    <div className="splash">
      <animated.img className='centered' src={score} alt="score!" style={{opacity: opacity, width: scale}}  />
    </div>
  )
}